import React, { useState , useEffect } from 'react';
import './VenueData.css';
import { useSelector,useDispatch } from 'react-redux';
import {  getAllVenues } from '../../actions/venueActions.js';
import { deleteVenue } from '../../actions/venueActions.js';

const VenueData = () => {
  const dispatch = useDispatch();
  // const {venues}= useSelector((state) => state.venues);
  const venues = useSelector((state) => state.venue.venues);



  
 
 

useEffect(()=>{
  dispatch(getAllVenues());
},[dispatch]);

  const [venues1, setVenues1] = useState([]);

  
  // Function to add a new venue to the table
  const addVenue = (formData) => {
    setVenues1([...venues1, formData]);
  };

 

  const deleteVenueHandler = (id) => {
    if (window.confirm("Are you sure you want to delete this venue?")) {
      dispatch(deleteVenue(id));
      window.location.reload();
    }
  };

  
  // Function to update a venue in the table
  const updateVenue = (index, formData) => {
    console.log("updated");
  
  };

  

  return (
    <div className="venue-data">
      <h2>Venue Data</h2>
      {venues.length === 0 ? (
        <p>No venues available.</p>
      ) : (
        <table>
          <thead>
            <tr>
           
              <th>Venue Name</th>
              <th>Venue Address</th>
              <th>Venue Type</th>
              <th>Venue Category</th>
              <th>Establishment Year</th>
              <th>Ownership Type</th>
              <th>Availability</th>
              <th>Actions</th>
            </tr>
          </thead>

          <tbody>
          {venues.map((venue) => (
        <tr key={venue.id}>
        <td>{venue.venueName}</td>
  <td>{venue.venueAddress}</td>
  <td>{venue.venueType}</td>
  <td>{venue.venueCategory}</td>
  <td>{venue.ownershipType}</td>
  <td>{venue.establishmentYear}</td>
  <td>{venue.availability[0]} To {venue.availability[1]} </td>
  
 
<tr>


  <td>
    <button className="delete-btn" onClick={() => deleteVenueHandler(venue._id)}>Delete</button>
    <button className="update-btn" onClick={() => updateVenue()}>Update</button>
  </td>
</tr>
          {/* Other venue details */}
        </tr>
      ))}
            
          </tbody>
        </table>
      )}
    </div>
  );
};

export default VenueData;


